@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;600;900&display=swap);
:root{
    --dark-bg-color: #161d2f;
    --light-bg-color: #4b5366;
    --light-orange: #fbbe88;
    --light-green: #569E78;
}

*{
    box-sizing: border-box;
}

body{
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
    font-weight: 400;
    color: rgba(255,255,255,0.9);
    background-image: url(https://yannickdedecker.github.io/Cookr/static/media/Background.3df2eb4b.png);
    background-repeat: repeat;
    background-position: center;
}

.btn-close{
    color: white;
}
.card-body{
    background: rgba(255,255,255,0.2);
}

.card{
    background: none;
    min-height: 240px;
}

.gallery-block{
	padding-bottom: 40px;
	padding-top: 40px;
}

.gallery-block .heading{
    margin-bottom: 50px;
    text-align: center;
}

.gallery-block .heading h2{
    font-weight: bold;
    font-size: 1.4rem;
    text-transform: uppercase;
}

.gallery-block.cards-gallery h6 {
  font-size: 18px;
  font-weight: 900; 
  text-transform: uppercase;
  color: white;
}

.gallery-block.cards-gallery .card{
  transition: 0.4s ease; 
}

.gallery-block.cards-gallery .card img {
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.15); 
}

.gallery-block.cards-gallery .card-body {
  text-align: center; 
}

.gallery-block.cards-gallery .card-body p {
  font-size: 15px; 
}

.gallery-block.cards-gallery a {
  color: #212529; 
}

.gallery-block.cards-gallery a:hover {
  text-decoration: none; 
}

.gallery-block.cards-gallery .card {
  margin-bottom: 30px; 
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.15);
}

.tag{
  position: absolute;
  left: 5px;
  top: 5px;
  background: var(--light-green);
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 0.75rem;
  padding: 2px;
  color: white;
}

.found{
  display: none;
}

@media (min-width: 576px) {

	.gallery-block .transform-on-hover:hover {
	    transform: translateY(-10px) scale(1.02);
	    box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.15) !important; 
	}
}

@media only screen and (max-width: 992px) {
  .gallery-block.cards-gallery h6 {
    font-size: 24px;
    font-weight: 900; 
    text-transform: uppercase;
    color: white;
  }
}
#showcase-container{
    background-image: url(https://yannickdedecker.github.io/Cookr/static/media/pexels-lumn-604969.214f0272.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    width: 100vw;
    max-width: 100%;
    height: 500px;
    margin-top: 2rem;
    margin-bottom: 2rem;
    overflow-x: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; 
    box-shadow: -1px 0px 7px 0px #000000;
}

#showcase-container h1{
    background-color: var(--light-green);
    padding: 10px 20px;
    font-size: 3.5rem;
    font-weight: 900;
    text-transform: uppercase;
    margin: 0 0 10px 0;
}

#showcase-container h2{
    text-transform: uppercase;
    letter-spacing: 5px;
    font-size: 1.2rem;
    color: white;
    text-shadow: -1px -1px 1px rgba(255,255,255,.1), 1px 1px 1px rgba(0,0,0,.5);
    margin: 0;
}

@media only screen and (max-width: 600px) {
    #showcase-container h1{
        background-color: var(--light-green);
        line-height: 1;
        padding: 10px 20px;
        font-size: 1.5rem;
        margin: 0 0 10px 0;
    }

    #showcase-container h2{
        text-transform: uppercase;
        text-align: center;
        letter-spacing: 5px;
        font-size: 1rem;
        color: white;
        text-shadow: -1px -1px 1px rgba(255,255,255,.1), 1px 1px 1px rgba(0,0,0,.5);
        margin: 0;
    }
}
.skills-container{
    padding: 50px 0;
}

.skills-container ul{
    list-style: none;
    margin: 0;
    padding: 0;
}

.designer{
    display: inline-block;
    background-color: var(--light-green);
    padding: 10px 15px;
    font-size: 2rem;
    font-weight: 900;
    text-transform: uppercase;
}

.webdesigner{
    display: inline-block;
    background-color: var(--light-orange);
    padding: 10px 15px;
    font-size: 2rem;
    font-weight: 900;
    text-transform: uppercase;
}

.skills-container h2{
    font-size: 1.5rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
    font-weight: 900;
}

.col-md-4{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

@media only screen and (max-width: 992px) {
    .pieChart{
        width: 200px;
        margin: 50px 0;
    }
}
.about-container{
  background-color: rgba(255,255,255,0.2);
  margin: 2rem 0 0 0;
  padding: 35px 30px 0 30px; 
  box-shadow: -1px 0px 7px 0px #000000;
}

.about-container .colAbout{
  padding-right: 60px;
}

.about-container img{
  max-width: 100%;
}

.about-container .link{
  text-decoration: none;
  color: var(--light-orange);
}

.about-container h1{
  display: inline-block;
  background-color: var(--light-green);
  padding: 10px 20px;
  font-size: 3rem;
  font-weight: 900;
  text-transform: uppercase;
  margin: 15px 0 25px 0;
}

.about-container span{
  font-weight: 600;
}

.about-container h2{
    margin-bottom: 2rem;
}

.about-container p{
    font-weight: 200;
}

.about-container p:nth-last-child(1){
  margin-bottom: 35px;
}

@media only screen and (max-width: 992px) {
  .colAbout{
    padding: 0;
    margin: 0;
  }
}
body {
  overflow-x: hidden;
}

.contact-container {
  background-color: rgba(255, 255, 255, 0.2);
  margin: 2rem 0 5rem 0;
  padding: 35px 30px 0 30px;
  box-shadow: -1px 0px 7px 0px #000000;
}

.contact-container h1 {
  display: inline-block;
  background-color: var(--light-green);
  padding: 10px 20px;
  font-size: 3rem;
  font-weight: 900;
  text-transform: uppercase;
  margin: 15px 0 25px 0;
}

.contact-container h2 {
  margin-bottom: 35px;
}

.contact-container ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.contact-container ul li {
  margin: 15px 0;
  font-size: 1rem;
  display: block;
}

.image-main {
  width: 30px;
  margin-right: 15px;
}

.contact-img {
  width: 100%;
  padding-bottom: 2rem;
}

@media only screen and (max-width: 768px) {
  .contact-img {
    display: none;
  }

  .contact-container{
    text-align: center;
    margin: 0;
    padding: 0;
  }

  .contact-container ul{
margin-bottom: 2rem;
  }

  .contact-container h1{
    padding: 10px 20px;
    font-size: 1.5rem;
    margin: 25px 0 25px 0;
  }

  .contact-container h2 {
    font-size: 1.5rem;
    padding: 0 30px;
  }
}

.form-group {
  background-image: url(https://yannickdedecker.github.io/Cookr/static/media/Background.3df2eb4b.png);
  padding: 0 0 50px 0;
  margin: 10px 0 2rem 0;
  border-radius: 20px;
}

.form-control {
  border: none;
}

.form-control:focus {
  border-color: none;
  box-shadow: none;
  outline: none;
}

.form-group label {
  color: rgba(255, 255, 255, 0.9);
  margin: 0;
}

.form-group input {
  background-color: rgba(255, 255, 255, 0.2);
  margin: 0 0 10px 0;
  border-radius: 0;
  color: white;
}

.form-group input:active,
.form-group input:focus {
  background-color: var(--light-orange);
  border-color: none;
  outline: none;
  color: var(--dark-bg-color);
}

textarea {
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.2);
  margin-bottom: 10px;
}

.submitbtn {
  width: 100%;
  margin-top: 15px;
  background-color: var(--light-green);
  border: none;
  color: white;
  padding: 10px 0;
}

.submitbtn:active {
  transform: scale(0.95);
}

.submitbtn:hover {
  background-color: #417f5d;
}

#search-bar:focus{
    outline: none;
}

#search-bar{
    border: none;
    color: white;
    font-size: 16px;
    font-weight: 300;
    background-color: rgba(255,255,255,0.2);
    border-radius: 15px;
    width: 400px;
    margin-left: 80px;
    padding: 8px 20px;
}

#search-bar::-webkit-input-placeholder{
    color: var(--dark-bg-color);
}

#search-bar:-ms-input-placeholder{
    color: var(--dark-bg-color);
}

#search-bar::placeholder{
    color: var(--dark-bg-color);
}

@media only screen and (max-width: 992px) {
    #search-bar{
        margin: 1rem auto;
        width: 250px;
        text-align: center;
    }
}
.navbar-brand{
    padding-bottom: 5px;
    margin-right: 4rem;
}

.navbar-brand img{
    height: 35px;
}
.nav-link{
    font-size: 14px;
    font-weight: 300;
    letter-spacing: 0.3px;
    text-transform: uppercase;
    margin: 0 10px;
}

.nav-item{
    width: 120px;
    text-align: center;
}

.active{
    background-color: var(--light-orange);
    padding: 8px 12px;
    color: var(--dark-bg-color) !important;
}

@media only screen and (max-width: 992px) {
    .navbar-nav{
        margin-top: 2rem;
    }

    .nav-item{
        margin: 0 auto;
    }
}
.footer-container{
    background-color: rgba(255,255,255,0.2); 
    box-shadow: -1px -4px 7px -4px #000000;
}

footer p{
    text-align: center;
    font-size: 0.75rem;
    margin: 0;
}
  
footer{
    padding: 15px 0;
}

footer span{
    font-weight: 600;
}

@media only screen and (max-width: 600px) {
    footer p{
        text-align: center;
        font-size: 0.5rem;
        margin: 0;
        padding: 0 25px;
    }
}
/* SHOWCASE */

#showcase-recipe-article{
    width: 100vw;
    max-width: 100%;
    height: 500px;
    margin-top: 2rem;
    margin-bottom: 2rem;
    overflow-x: hidden; 
    box-shadow: -1px 0px 7px 0px #000000;
}

#showcase-recipe-article h1{
    background-color: var(--light-green);
    padding: 10px 20px;
    font-size: 3.5rem;
    font-weight: 900;
    text-transform: uppercase;
    margin: 25px 0 15px 0;
}

#showcase-recipe-article li{
    text-transform: uppercase;
    letter-spacing: 5px;
    font-size: 2.5rem;
    color: white;
    text-shadow: -1px -1px 1px rgba(255,255,255,.1), 1px 1px 1px rgba(0,0,0,.5);
    margin: 0;
}

.content{
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.4);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

/* TAG LIST */

#tag-list{
    display: flex;
    list-style: none;
    color: var(--light-orange);
    text-transform: uppercase;
    letter-spacing: 5px;
    margin: 0 0 10px 0;
    padding: 0;
}

#tag-list li{
    font-size: 1rem;
}

#tag-list li:not(:nth-of-type(1))::before{
    content: '|';
    margin-left: 5px;
}

/* INSTRUCTIONS */

#instructions-list{
    list-style: none;
    padding: 0;
    margin: 0;
}

#instructions-list li{
    margin-bottom: 15px;
    padding-bottom: 15px;
    border-bottom: 2px dotted rgba(255,255,255,0.4);
}

#instructions-list li span{
    color: var(--light-orange);
    font-weight: bold;
    font-size: 1rem;
    margin-right: 5px;
}

#col-left{
    color: var(--dark-bg-color);
}

#white-background{
    background: white;
    border-radius: 15px;
    padding: 35px 20px 35px 20px;
}

#col-right{
    padding: 25px 50px 25px 50px;
}

#col-right img{
    position: absolute;
    width: 20%;
    right: 110px;
    top: 130px;
}

#col-right h1{
    font-weight: 900;
    line-height: 4rem;
    text-transform: uppercase;
    font-size: 4rem;
}

#col-right h2{
    color: var(--light-green);
    font-size: 0.7rem;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 0.25rem;
    margin-bottom: 1rem;
}

#col-right h3{
    margin-top: 1rem;
    margin-bottom: 1rem;
    font-weight: 900;
    font-size: 1.35rem;
}

#col-right p span{
    color: var(--light-green);
    font-weight: 900;
    margin-right: 10px;

}

#col-left h3{
    margin-top: 2.5rem;
    margin-bottom: 1rem;
    font-weight: 900;
    font-size: 1.35rem;
    color: var(--dark-bg-color)
}

#main-recipe-container{
    margin-top: 5rem;
}

#default-list{
    list-style: none;
    justify-content: center;
    align-items: center;
    background-color: var(--light-bg-color);
    border-radius: 15px;
    color: white;
}

#default-list p{
    margin: 10px 10px;
    font-size: 0.8rem;
    text-align: center;
}

#ingredients-list{
    list-style: none;
    margin: 0 10px 0;
    padding: 0;
}

#ingredients-list li{
    margin: 10px 0;
    font-size: 0.95rem;
}

.fa-clock, .fa-award{
    color: var(--light-green);
    margin: 0;
}

.fa-check-circle{
    color: var(--light-green);
    margin: 0 5px 0 5px;
}
